.name_two_block {
    display: flex;
    align-items: center;
}

.projBox:hover {
    opacity: 0.5;
    cursor: pointer !important;
    transition: .2s;
}


.line_two {
    width: 100px;
    height: 1px;
    background: black;
}

.just_name {
    margin-left: 15px;
}

.img_item_block {
    width: 100%;
    height: 250px;
    object-fit: cover;
}

.img_item_block img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.title_1 {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    margin: 30px 0;
}

.text_1 {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 147.5%;
    color: #525560;
}

