.card_name {
    color: #fff;
    max-width: 300px;
    padding: 40px 0 20px 20px !important;
}

.text {
    color: #fff;
    max-width: 300px;
    padding: 40px 0 20px 20px !important;
}

.see_more {
    color: #fff;
    padding: 10px 30px;
    background: #03b4a0;
    margin-left: 30px;
    border-radius: 10px;
    margin-top: 40px;
    border: 1px solid #03B4A0;
    font-weight: bold;
}

.see_more:hover {
    background: white;
    border: 1px solid #03B4A0;
    color: #03B4A0;
    transition: .2s;
}

